* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.header {
  display: flex;
  gap: 10px;
  align-items: center;
}
.info-span {
  font-size: 18px;
  font-weight: 400;
  margin-left: 10px;
}
.countri-array {
  font-size: 24px;
  font-weight: 400;
  margin-left: 10px;
}
